<template>

  <v-app>
    <v-navigation-drawer v-model="sidebar" app>
      <v-list>
        <v-list-tile
          v-for="item in menuItems"
          :key="item.title"
          :to="item.path">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>{{ item.title }}</v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar app>
      <span class="hidden-sm-and-up">
        <v-toolbar-side-icon @click="sidebar = !sidebar">
        </v-toolbar-side-icon>
      </span>
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer">
          {{ appTitle }}
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn
          flat
          v-for="item in menuItems"
          :key="item.title"
          :to="item.path">
          <v-icon left dark>{{ item.icon }}</v-icon>
          {{ item.title }}
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-content>
      <router-view></router-view>
    </v-content>

  </v-app>


</template>

<script>
import Loading from '@/components/Loading.vue'
import {mapState, mapActions} from 'vuex'
export default {
  name: 'App',

  components: {
    Loading
  },

  data: () => ({
    appTitle: 'Awesome App',
      sidebar: false,
      menuItems: [
          { title: 'Home', path: '/', icon: 'mdi-account' },
          { title: 'Sign Up', path: '/signup', icon: 'mdi-account' },
          { title: 'Sign In', path: '/signin', icon: 'mdi-account' }
     ],


    items: [
      {name: "Profile", to: '/profile', icon: "mdi-account"},
      {name: "My Courses", to: '/courses', icon: "mdi-school"},
      {name: "My Cart", to: '/cart', icon: "mdi-cart"},
      {name: "Wishlist", to: '', icon: "mdi-view-list"},
      {name: "Help", to: '', icon: "mdi-help-box"},
      {name: "Log Out", to: '', icon: "mdi-logout"},
    ],
  }),
  methods: {
    ...mapActions(['isAuth'])
  },
  created() {
    this.isAuth();
  },
};
</script>

<style>
  .goldColor {
    background-color: rgb(187, 162, 87);
  }

  .v-btn--active .v-btn__content {
    color: rgb(187, 162, 87);
  }
</style>
